::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: none;
}

::-webkit-scrollbar-thumb {
  background-color: #a0a0a0;
  border-radius: 5px;
  min-height: 40px;
  transition: background-color .2s ease-in-out;
}

::-webkit-scrollbar-thumb:hover {
  background-color: gray;
}

::-moz-scrollbar {
  width: 10px;
}

::-moz-scrollbar-track {
