body {
  min-height: 100vh;
  margin: 0;
  font-family: Roboto, sans-serif;
}

.navbar {
  color: #333;
  background-color: #f5f3f5;
  padding: 1rem;
}

.navbar ul {
  justify-content: space-around;
  list-style: none;
  display: flex;
}

.navbar ul li {
  margin: 0 1rem;
}

.navbar ul li:hover {
  color: #c7bbc7;
  cursor: pointer;
}

@media (width <= 760px) {
  .navbar ul {
    flex-direction: column;
  }
}

.video-container {
  z-index: -1;
  place-items: center;
  height: 100vh;
  display: grid;
  position: relative;
}

.video-container video {
  object-fit: cover;
}

.overlay {
  background-color: #00000080;
  place-items: center;
  height: 100vh;
  display: grid;
  position: relative;
}

.overlay h1 {
  color: gold;
  z-index: 3;
  font-size: clamp(3.5rem, 5vw, 6rem);
  font-weight: 800;
  animation: 1s ease-in-out infinite alternate colorChange;
  position: relative;
}

@keyframes colorChange {
  from {
    color: #998100;
  }

  to {
    color: #c88304;
  }
}

.overlay p {
  color: #c88304;
  text-align: center;
  font-size: clamp(1rem, 5vw, 1.5rem);
  font-weight: 200;
  position: relative;
}

@font-face {
  font-family: Indie Flower;
  src: url("https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap");
}

@font-face {
  font-family: Raleway;
  src: url("https://fonts.googleapis.com/css2?family=Raleway:wght@300&display=swap");
}

.v-0-0-3-page {
  --v-0-0-3-text-color: #333;
  --v-0-0-3-primary-color: #150050;
  --v-0-0-3-secondary-color: #3f0071;
  --v-0-0-3-accent-color: #582707;
  --v-0-0-3-highlight-color: #c88304;
  --v-0-0-3-background-color: #f5f3f5;
  --v-0-0-3-box-shadow-color: #15005099;
  --v-0-0-3-box-shadow-second-color: #3f007199;
  --v-0-0-3-box-shadow-offset-x: 5px;
  --v-0-0-3-box-shadow-blur-radius: 5px;
  --v-0-0-3-box-shadow-ie-filter: progid:DXImageTransform. Microsoft. Blur(PixelRadius= 3, MakeShadow= true, ShadowOpacity= .3);
}

.v-0-0-3-page *, .v-0-0-3-page :before, .v-0-0-3-page :after {
  box-sizing: border-box;
}

.v-0-0-3-page body {
  letter-spacing: .05em;
  min-height: 100%;
  margin: 0;
  font-family: Raleway, sans-serif;
  font-weight: 300;
  line-height: 1.6;
  overflow-x: hidden;
}

.v-0-0-3-page .wrapper {
  background-color: var(--v-0-0-3-primary-color);
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}

.v-0-0-3-page .shadow1 {
  background-color: var(--v-0-0-3-box-shadow-color);
  -moz-box-shadow: var(--v-0-0-3-box-shadow-offset-x) var(--box-shadow-offset-y) var(--v-0-0-3-box-shadow-blur-radius) var(--v-0-0-3-box-shadow-color);
  -webkit-box-shadow: var(--v-0-0-3-box-shadow-offset-x) var(--box-shadow-offset-y) var(--v-0-0-3-box-shadow-blur-radius) var(--v-0-0-3-box-shadow-color);
  box-shadow: var(--v-0-0-3-box-shadow-offset-x) var(--box-shadow-offset-y) var(--v-0-0-3-box-shadow-blur-radius) var(--v-0-0-3-box-shadow-color);
  filter: var(--v-0-0-3-box-shadow-ie-filter);
  -ms-filter: var(--v-0-0-3-box-shadow-ie-filter);
  zoom: 1;
  margin: 40px;
}

.v-0-0-3-page .shadow1 .box {
  color: var(--v-0-0-3-primary-color);
  border: 1px solid var(--v-0-0-3-secondary-color);
  background-color: var(--v-0-0-3-background-color);
  border-radius: 8px;
  height: auto;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 20px;
  position: relative;
}

.v-0-0-3-page h1, .v-0-0-3-page h2 {
  font-family: Indie Flower, cursive;
}

.v-0-0-3-page h2 {
  margin: 1em 0;
  font-size: 3rem;
  line-height: 1;
}

.v-0-0-3-page a {
  color: var(--v-0-0-3-highlight-color);
}

.v-0-0-3-page header {
  background-color: var(--v-0-0-3-background-color);
  z-index: 999;
  flex-direction: column;
  align-items: center;
  width: calc(100vw - 17px);
  padding: 20px 0;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.v-0-0-3-page header h1 {
  text-align: center;
  font-size: x-large;
  font-style: oblique;
  font-weight: 500;
}

.v-0-0-3-page header img {
  max-width: 150px;
  height: auto;
  margin: 40px auto 0;
  padding: 1em;
}

.v-0-0-3-page header ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.v-0-0-3-page header ul li:not(:last-child) {
  margin-right: 2em;
}

.v-0-0-3-page header ul li a {
  color: var(--v-0-0-3-highlight-color);
  text-decoration: none;
}

.v-0-0-3-page header ul li:hover a {
  color: var(--v-0-0-3-primary-color);
  text-decoration: underline;
}

.v-0-0-3-page header ul li:visited a {
  color: var(--v-0-0-3-secondary-color);
}

.v-0-0-3-page header ul li:active a {
  color: var(--v-0-0-3-accent-color);
}

.v-0-0-3-page murphy {
  background-color: var(--v-0-0-3-background-color);
  justify-content: center;
  align-items: center;
  display: flex;
}

.v-0-0-3-page murphy img {
  box-shadow: 0px 4px 6px var(--v-0-0-3-box-shadow-color), 20px 0px 6px var(--v-0-0-3-box-shadow-second-color);
  border-radius: 75px;
  max-width: 100%;
  height: auto;
  margin: 10em 5em 5em;
  padding: 10em;
}

.v-0-0-3-page article {
  width: 100%;
  margin-top: 9vh;
  position: relative;
}

.v-0-0-3-page article:last-child {
  margin-bottom: 88vh;
}

.v-0-0-3-page article picture.backdrop {
  width: 100%;
  height: 99%;
  position: absolute;
  top: 0;
  left: 0;
}

.v-0-0-3-page article picture.backdrop img {
  object-fit: cover;
  object-position: bottom;
  filter: blur(90px) brightness(95%) contrast(70%);
  width: 100%;
  height: 88%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scaleX(1.5)scaleY(1.14);
}

.v-0-0-3-page article .front {
  text-align: center;
  box-sizing: border-box;
  perspective: 100vh;
  max-width: 800px;
  margin: auto;
  padding: 20px;
  position: relative;
  transform: translateY(70px);
}

.v-0-0-3-page article .front:after {
  content: "";
  background-color: var(--v-0-0-3-background-color);
  box-shadow: var(--v-0-0-3-box-shadow-color);
  width: 80%;
  height: 8px;
  position: absolute;
  bottom: 0;
  left: 10%;
  transform: translateY(-26px);
}

.v-0-0-3-page article .front img {
  box-shadow: 0px 4px 6px var(--v-0-0-3-box-shadow-color), 20px 0px 6px var(--v-0-0-3-box-shadow-second-color);
  width: 100%;
  height: auto;
  margin-top: 160px;
  position: relative;
}

.v-0-0-3-page article h2 {
  animation-name: focus-in-contract;
  animation-duration: 3s;
  animation-timing-function: linear;
}

.v-0-0-3-page footer {
  background-color: var(--v-0-0-3-primary-color);
  color: var(--v-0-0-3-highlight-color);
  z-index: 333;
  justify-content: center;
  padding: 5px;
  display: flex;
  position: relative;
}

.v-0-0-3-page .social-media-icons {
  text-align: center;
  font-family: "Font Awesome 5 Free";
  font-size: x-large;
  position: relative;
  top: 3em;
}

.v-0-0-3-page .social-media-icons i {
  cursor: pointer;
  transition: transform .5s;
}

.v-0-0-3-page .social-media-icons i:hover {
  transform: scale(1.1);
}

@keyframes focus-in-contract {
  0% {
    letter-spacing: 1em;
    filter: blur(12px);
    opacity: 0;
  }

  100% {
    letter-spacing: 0;
    filter: blur();
    opacity: 1;
  }
}

@media (width <= 480px) {
  .v-0-0-3-page header {
    width: 100vw;
    padding: 0;
  }
}

.v-0-0-3-page .hamburger {
  display: none;
}

@media screen and (width <= 600px) {
  .v-0-0-3-page .hamburger {
    cursor: pointer;
    display: block;
  }

  .v-0-0-3-page #navbar {
    display: none;
  }

  .v-0-0-3-page .active #navbar {
    display: block;
  }
}

